import { Component } from "react";
import { connect } from "react-redux";
import axios from "axios";

// Functions
import {
  stateSetSchedule,
  saveCalendars,
  stateSaveCourses,
  saveSections,
} from "../../state/actions";

// Components
import ClassList from "./ClassSection/ClassList";
import Calendar from "./Calendar/Calendar";
import Recommended from "./Recommended";
import Cart from "./Cart/Cart";

// Redux
const mapDispatchToProps = (dispatch) => {
  return {
    stateSaveCourses: (classes) => dispatch(stateSaveCourses(classes)),
    saveCalendars: (calendars) => dispatch(saveCalendars(calendars)),
    stateSetSchedule: (calendar) => dispatch(stateSetSchedule(calendar)),
    saveSections: (sections) => dispatch(saveSections(sections)),
  };
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      recommendedOpen: true,
    };

    this.toggleMenu = this.toggleMenu.bind(this);
  }

  toggleMenu(e, val) {
    if (typeof val !== "undefined")
      // Toggle the recommended menu
      this.setState({ recommendedOpen: val });
    else this.setState({ recommendedOpen: !this.state.recommendedOpen });
  }

  componentWillMount() {
    // Get all classes
    axios.get("http://localhost:8000/api/classes/").then((response) => {
      this.props.stateSaveCourses(response.data);
    });

    // Get calendars
    axios.get("http://localhost:8000/api/calendars/").then((response) => {
      this.props.saveCalendars(response.data);
      this.props.stateSetSchedule(response.data[0]);
      this.props.saveSections(response.data[0].sections);
    });
  }

  render() {
    return (
      <div
        className="flex justify-center items-center h-full bg-blue-300 p-4"
        style={{ paddingTop: 72 }}
      >
        <div className="flex justify-center items-center h-full w-full">
          <div className="flex flex-col w-1/4 2xl:w-1/5 h-full overflow-hidden">
            <ClassList
              open={this.state.recommendedOpen}
              toggleMenu={this.toggleMenu}
            />
            <Recommended
              open={this.state.recommendedOpen}
              toggleMenu={this.toggleMenu}
            />
          </div>
          <div className="shadow-xl w-3/4 2xl:w-4/5 ml-4 h-full flex">
            <Calendar />
            <Cart />
          </div>
        </div>
      </div>
    );
  }
}

export default connect(null, mapDispatchToProps)(Main);
